* {
  background-color: #23252c;
  color: #ffffff;
}
.App {
  text-align: center;
  width: 317px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  border-radius: 10px 10px 10px 10px;
}

.App,
.profile,
.profile img,
.middle-Content h2,
.middle-Content h3,
.middle-Content h5,
.social-Media,
.middle-Content P,
.footer {
  background-color: #1a1b21;
  cursor: default;
}

.profile {
  border-radius: 10px 10px 0px 0px;
}

.profile img {
  width: 317px;
  height: 317px;
  flex-shrink: 0;
  border-radius: 10px 10px 0px 0px;
  overflow: hidden;
}

.middle-Content {
  background-color: #1a1b21;
}

.middle-Content h2 {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 25px;
  margin-top: 21px;
  margin-bottom: 0;
}

.middle-Content h3 {
  font-family: "Inter", sans-serif;
  font-size: 12.8px;
  font-weight: 400;
  margin-top: 6.19px;
  color: #f3bf99;
}

.middle-Content h5 {
  font-family: "Inter", sans-serif;
  font-size: 10.24px;
  font-weight: 400;
  margin-top: 6.19px;
}

.social-Media {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
  width: 100%;
}

.emailBtn,
.linkedinBtn {
  border-radius: 6px;
  width: 115px;
  padding: 9px 13px 9px 11px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  font-weight: 500;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.emailBtn {
  background-color: #ffffff;
  color: #374151;
  justify-content: space-evenly;
}

.linkedinBtn {
  background-color: #5093e2;
  color: #ffffff;
  justify-content: space-evenly;
}

.middle-Details {
  width: 247px;
  text-align: left;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 33px;
  background-color: #1a1b21;
}

.middle-Details h3 {
  font-size: 16px;
  font-weight: 700;
  color: #ffffff;
  margin-bottom: 0%;
}

.middle-Details p {
  font-size: 10.24px;
  font-weight: 400;
  margin-top: 5px;
}

.footer {
  margin-top: 24px;
  border-radius: 0px 0px 10px 10px;
  background-color: #161619;
  height: 61px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.footer-Icons {
  width: 247px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #161619;
  cursor: pointer;
}
